@keyframes gradient {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}
body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: Arial, sans-serif;
  background: url('https://png.pngtree.com/thumb_back/fw800/background/20190223/ourmid/pngtree-classic-blue-corporate-annual-meeting-background-design-wallblueatmosphereannual-meeting-backgroundannual-image_73847.jpg') no-repeat center center fixed;
  background-size: cover;
}
.container {
  background-color: rgba(245, 245, 245, 0.9);
  border: 1px solid #bbb;
  border-radius: 10px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  width: 600px;
  padding: 50px;
  text-align: center;
}
h1 {
  color: #2c3e50;
  font-size: 24px;
  margin-bottom: 20px;
}
form {
  margin-bottom: 20px;
}
input {
  width: calc(100% - 22px);
  padding: 10px;
  margin: 5px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
}
button {
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}
button:hover {
  background-color: #0056b3;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  background-color: rgba(255, 255, 255, 0.9);
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 10px;
  margin: 5px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}
.employee-details {
  color: #333;
  font-weight: bold;
  text-align: left;
  width: 100%;
}
.delete-btn {
  background-color: #d9534f;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  cursor: pointer;
}
.delete-btn:hover {
  background-color: #c9302c;
}
.lgpd-link {
  display: block;
  margin-top: 20px;
  font-size: 14px;
  color: #007bff;
  text-decoration: none;
}
.lgpd-link:hover {
  text-decoration: underline;
}
.lgpd-btn {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}
.lgpd-btn:hover {
  background-color: #218838;
}
